@import url('https://fonts.googleapis.com/css?family=Source+Code+Pro:300|600');
body {
  margin: 0;
  padding: 0;
  font-family: 'Source Code Pro', sans-serif !important;
  color: #444;
  margin-bottom: 50px;
  background: #fefefe;
}

h1, h2, h3, h4 {
	color: #097b4e;
}

#mc_embed_signup input,
input {
  font-family: 'Source Code Pro', sans-serif;
  -webkit-transition: border 0.3s, -webkit-box-shadow 0.3s;
  transition: border 0.3s, -webkit-box-shadow 0.3s;
  -o-transition: box-shadow 0.3s, border 0.3s;
  transition: box-shadow 0.3s, border 0.3s;
  transition: box-shadow 0.3s, border 0.3s, -webkit-box-shadow 0.3s;
  border: 1px solid #59b1ef !important;
  -webkit-box-shadow: 0px 4px 10px #dcdada !important;
          box-shadow: 0px 4px 10px #dcdada !important;
  top: -2px;
  position: relative;
  border-radius: 8px !important;
}

#mc_embed_signup input:focus,
input:focus {
  -webkit-box-shadow: 0px 4px 10px #bbb !important;
          box-shadow: 0px 4px 10px #bbb !important;
  border: 1px solid #fefefe !important;
}

#mc_embed_signup .btn,
.btn {
  font-family: 'Source Code Pro', sans-serif;
  -webkit-transition: border 0.3s, background 0s, -webkit-box-shadow 0.3s;
  transition: border 0.3s, background 0s, -webkit-box-shadow 0.3s;
  -o-transition: box-shadow 0.3s, border 0.3s, background 0s;
  transition: box-shadow 0.3s, border 0.3s, background 0s;
  transition: box-shadow 0.3s, border 0.3s, background 0s, -webkit-box-shadow 0.3s;
  -webkit-box-shadow: 0px 4px 10px #dcdada;
          box-shadow: 0px 4px 10px #dcdada;
  font-weight: 800;
  font-size: 1.2rem;
  border: 1px solid #fff !important;
  border-radius: 8px;
  color: #fff;
  background: #59b1ef;
}

#mc_embed_signup .btn:hover, #mc_embed_signup .btn:active, #mc_embed_signup .btn:focus,
.btn:hover, .btn:active, .btn:focus {
  color: #fff;
  -webkit-box-shadow: 0px 4px 10px #bbb !important;
          box-shadow: 0px 4px 10px #bbb !important;
  border: 1px solid #fff !important;
}




